@import "../../assets/styles/variables.scss";
@import "../../assets/styles/mixins.scss";

.wrap {
  border: 0;
  position: relative;
  cursor: pointer;
  padding: 2px 6px;
  min-height: 22px;
  color: #161616;
  font-family: inherit;
  outline: none;
  border-radius: 4px;
  padding: 2px 6px;
  min-height: 26px;
  outline: none;
  background-color: $gray;
  box-shadow: 0px 1px 2px rgba(black, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  &:active {
    opacity: 0.6;
  }
  @include max-vw($lg) {
    margin-right: 16px;
  }
  @include min-vw($lg) {
    width: 100%;
    margin-top: 12px;
  }
  @include focus();
  img {
    pointer-events: none;
    opacity: 0.8;
    margin-right: 4px;
  }
}
